.wrapper{
    .listHeader{
        display: flex;
        align-items: center;

        background: #2196F3;
        border-radius: 15px;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
        margin-bottom: 15px;

        .number{
            width: 4.3%;
            font-family: Roboto;
            font-size: 18px;
            font-weight: 700;
            text-align: center;
            color: #fff;

            border-right: 1px #fff solid;
            padding: 22px 0;
        }
        .country{
            width: 64.4%;

            font-family: Roboto;
            font-size: 18px;
            font-weight: 700;
            color: #fff;

            padding: 22px 0 22px 15px;
            border-right: 1px #fff solid;
        }
        .cases{
            width: 31.3%;

            font-family: Roboto;
            font-size: 18px;
            font-weight: 700;
            color: #fff;

            padding: 22px 0 22px 15px;
        }
    }
    @media (min-width: 1680px){
        .listHeader{
            border-radius: 20px;
            margin-bottom: 20px;

            .number, .country, .cases{
                font-size: 24px;

                padding-top: 26px;
                padding-bottom: 26px;
            }
        }

    }
}